.floating-message {
  position: fixed;
  top: 10px;
  left: 40%;
  display: flex;
  align-items: center;
  transform: translateX(-50%);
  background-color: var(--color-error);
  color: var(--color-text-light);
  padding: 10px 20px;
  border-radius: 5px;
  z-index: 1200;
  animation: slideIn 0.5s ease forwards, fadeOut 3.5s ease 2.5s forwards;
}
.alert-icons {
  color: var(--color-text-light);
  margin-right: 7px;
}
.floating-message.success {
  background-color: var(--color-success);
  color: var(--color-text-light);
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
