/* AttachedFilesUploader.css */

.attached-files {
  position: relative;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.file-uploader {
  display: flex;
  align-items: flex-start;
  position: relative;
}

.file-uploader-button {
  padding: 10px;
  background-color: var(--color-primary-dark);
  color: var(--color-primary-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.file-uploader-button:hover {
  background-color: var(--color-secondary-dark);
  color: var(--color-primary-light);
}

.file-list-container {
  background-color: var(--color-secondary-light);
  border-radius: 8px;
  min-width: 250px;
  width: min-content;
  position: absolute;
  bottom: 0%;
  left: 120%;
  border: 1px solid var(--color-border);
}

.toggle-list-button {
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 6px 7px;
  background-color: var(--color-secondary-light);
  border-radius: 8px;
  transition: background-color 0.3s;
}

.toggle-list-button:hover {
  background-color: var(--color-primary-light);
}

.file-list {
  list-style: none;
  margin: 0;
  padding: 0 7px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding 0.3s ease-out;
}

.file-list.visible {
  max-height: 400px; /* adjust as needed */
  padding: 10px;
  transition: max-height 0.5s ease-in, padding 0.3s ease-in;
}

.document-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  padding: 7px;
  border-bottom: 1px solid var(--color-border);
  cursor: pointer;
}

.document-item:last-child {
  border-bottom: none;
}

.download-icon-name {
  max-width: 250px;
  word-break: break-word;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--color-primary-dark);
}

.document-item:hover div,
.document-item:hover .download-icon {
  color: var(--color-highlight-dark);
}

.file-actions {
  margin-left: auto;
  display: flex;
  gap: 10px;
}

.file-icon {
  cursor: pointer;
  color: var(--color-secondary-dark);
  transition: color 0.3s ease;
  width: 18px;
}

.file-icon.success {
  color: var(--color-success);
}

.file-icon.error {
  color: var(--color-error);
}

.file-icon.delete:hover {
  color: var(--color-error);
}

.views-documents {
  position: absolute;
  bottom: 7px;
  margin: 0px;
  background-color: var(--color-secondary-light);
  border: 1px solid var(--color-border);
  border-radius: 9px;
}

.document-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding: 5px 10px;
  background-color: var(--color-secondary-light);
  border-radius: 8px;
  transition: background-color 0.3s;
}

.document-header:hover {
  background-color: var(--color-primary-light);
}
.document-header p {
  margin: 0;
}
.chevron-icon {
  color: var(--color-text-dark);
  transition: transform 0.3s;
}

.documents-list {
  list-style-type: none;
  padding: 0px 7px;
  margin: 0;
  max-height: 0;
  min-width: 350px;
  overflow: hidden;
  transition: max-height 0.3s ease-out, padding 0.3s ease-out;
}

.documents-list.expanded {
  max-height: 400px;
  padding: 7px;
  transition: max-height 0.5s ease-in, padding 0.3s ease-in;
}
