* {
  box-sizing: border-box;
}

.form-line {
  display: flex;
  flex-wrap: nowrap;
  gap: 5px;
  width: 100%;
}

.form-group {
  flex: 1 1 150px;
  transition: flex 0.3s ease, min-width 0.3s ease, max-width 0.3s ease;
  min-width: 50px;
  max-width: 100%;
  box-sizing: border-box;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.form-group.focused {
  flex: 3 1 200px;
  min-width: 200px;
}
.autocomplete-container {
  flex: 1 1 470px;
  transition: flex 0.3s ease, min-width 0.3s ease, max-width 0.3s ease;
  min-width: 150px;
  box-sizing: border-box;
}

.autocomplete-container.focused {
  flex: 3 1 450px;
  min-width: 200px;
}
.autocomplete-container-equipment {
  flex: 1 1 450px;
  transition: flex 0.3s ease, min-width 0.3s ease, max-width 0.3s ease;
  min-width: 150px;
  box-sizing: border-box;
}

.autocomplete-container .equipment.focused {
  flex: 3 1 450px;
  min-width: 200px;
}

.request-form {
  background-color: var(--color-background);
  border-radius: 15px;
  padding: 7px;
}

.request-form .form-group label {
  display: block;
  font-weight: normal;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
  font-style: italic;
  color: var(--color-text-dark);
}

.request-form .form-group input,
.request-form .form-group select {
  padding: 10px;
  border: 1px solid transparent;
  border-radius: 10px;
  background-color: var(--color-primary-light);
  color: var(--color-secondary-dark);
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
  width: 100%;
  box-sizing: border-box;
  transition: padding 0.3s ease;
}

.request-form .form-group input:focus,
.request-form .form-group select:focus {
  outline: none;
  border-color: var(--color-highlight-dark);
  background-color: var(--color-secondary-light);
}

.form-group label.required {
  color: var(--color-error);
  margin-left: 5px;
}

.form-group input.error,
.form-group .error {
  border: 1px solid var(--color-error);
  border-radius: 11px;
}

.list-code {
  border: 1px solid var(--color-border);
  color: var(--color-text-dark);
}

.form-group.input-quantity {
  min-width: 90px;
  max-width: 90px;
}

.form-group.input-baseUnit {
  min-width: 150px;
}

.popover-content {
  max-width: 300px;
  max-height: 200px;
  overflow: auto;
  background-color: white;
  border: 1px solid #ddd;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Responsive styles */
@media (max-width: 1064px) {
  .form-line {
    flex-wrap: wrap;
  }

  th,
  td {
    padding: 4px;
  }
}
