/* RegistrationModal.css */

.registration-modal {
  min-width: 30%;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 9999;
  transform: translate(-50%, -50%);
  background-color: var(--color-background);
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid var(--color-border);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.5s ease-in-out;
  color: var(--color-secondary-dark);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -60%);
  }
  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

.registration-modal h2 {
  color: var(--color-secondary-dark);
  margin: 0px;
  text-align: center;
}

.registration-modal .input-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.registration-modal label {
  display: block;
  color: var(--color-text-dark);
  margin-top: 10px;
  font-weight: bold;
}

.registration-modal input,
.registration-modal select {
  background-color: var(--color-secondary-light);
  width: 100%;
  padding: 10px;
  border: 1px solid var(--color-border);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.registration-modal input:focus,
.registration-modal select:focus {
  border-color: var(--color-secondary-dark);
  box-shadow: 0 0 8px rgba(98, 0, 234, 0.1);
}

.registration-modal .password-container {
  display: flex;
  align-items: center;
  position: relative;
}

.registration-modal .toggle-password {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  color: var(--color-primary-dark);
  cursor: pointer;
  font-size: 14px;
}

.registration-modal .generate-password {
  background-color: var(--color-secondary-dark);
  color: var(--color-text-light);
  border: none;
  border-radius: 8px;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.registration-modal .generate-password:hover {
  background-color: var(--color-primary-dark);
}

.registration-modal .role-toggle-buttons {
  background-color: var(--color-border);
  display: flex;
  gap: 10px;
  border-radius: 8px;
  border: none;
}

.registration-modal .role-button {
  flex: 1;
  padding: 10px;
  background-color: var(--color-secondary-light);
  border: 5px solid var(--color-border);
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.registration-modal .role-button.active {
  background-color: var(--color-secondary-dark);
  color: var(--color-text-light);
  border: 5px solid var(--color-border);
  border-radius: 9px;
}

.registration-modal .role-button.active:hover {
  background-color: var(--color-primary-dark);
  color: var(--color-text-light);
  border: 5px solid var(--color-border);
}
.registration-modal .role-button:hover {
  background-color: var(--color-primary-light);
  border: 5px solid var(--color-border);
}

.registration-modal-footer {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  margin-top: 20px;
}
.registration-modal-footer button {
  padding: 10px;
  min-width: 100px;
  background-color: var(--color-primary-dark);
  color: var(--color-text-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.registration-cancel-button:hover {
  background-color: var(--color-secondary-dark);
  color: var(--color-text-light);
}
.registration-submit:hover {
  background-color: var(--color-highlight-light);
  color: var(--color-text-light);
}
.error-message {
  color: var(--color-error);
  margin-top: 10px;
  text-align: center;
}
