.product-table {
  width: 100%;
  max-height: 60vh;
  border-collapse: separate;
  font-size: 13px;
  color: var(--color-text-dark);
  overflow-y: auto;
}

.table-controls-product {
  position: sticky;
  z-index: 2;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  margin: 0;
  padding: 0;
  background-color: var(--color-background);
}

.table-controls-product .custom-checkbox {
  margin: 0;
  font-size: 13px;
}

.product-table td {
  border: 1px solid transparent;
  padding: 4px;
  text-align: left;
}

.product-table td link {
  word-break: break-word;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-table th {
  background-color: var(--color-primary-light);
  text-align: left;
  border: 1px solid var(--color-primary-light);
  font-weight: 600;
  border-bottom: none;
  vertical-align: middle;
  padding: 6px;
  color: var(--color-text-dark);
  position: sticky;
  top: 0;
  z-index: 1;
}
.product-table th:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.product-table th:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.product-table tr:nth-child(even) {
  background-color: var(--color-secondary-light);
}

.product-table tr:hover {
  background-color: var(--color-hover-row);
}

.product-table td[contenteditable="true"] {
  border: 1px dashed var(--color-highlight-light);
  word-break: break-word;
}

.product-table td.editable-cell,
.product-table td.link {
  border: 1px dashed var(--color-highlight-light);
  background-color: var(--color-input-bg);
}

.product-table tr.editing-row {
  border: 1px dashed var(--color-highlight-light);
  background-color: var(--color-input-bg);
  border-radius: 5px;
}

.product-table tr.editing-row td {
  border: 1px dashed var(--color-highlight-light);
}

.product-table tr.editing-row td:first-child {
  border-left: 2px solid var(--color-highlight-light);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.product-table tr.editing-row td:last-child {
  border-right: 2px solid var(--color-highlight-light);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.product-table td.error-cell {
  border: 1px dashed var(--color-error);
  background-color: #ffe6e6;
}

.product-table button {
  margin-right: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.export-button {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--color-background);
  border: 1px solid var(--color-border);
}
.export-button:hover .icons {
  color: var(--color-success);
}

.link-icon {
  cursor: pointer;
  color: var(--color-highlight-light);
}

.link-icon:hover {
  color: var(--color-highlight-dark);
}

.product-table td.link-cell {
  max-width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}

.product-table td.link-cell[contenteditable="true"] {
  max-width: 150px;
}

.status-dropdown {
  font-size: 13px;
  background-color: var(--color-background);
}

.action-icons {
  display: flex;
  align-items: center;
  z-index: 2;
}

.action-icons button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px;
  margin-right: 5px;
  cursor: pointer;
  font-size: 15px;
  background-color: transparent;
  color: var(--color-secondary-dark);
  transition: background-color 0.3s ease;
}

.action-icons button:hover {
  background-color: transparent;
}

.button-product-edit:hover {
  color: var(--color-highlight-dark);
  background-color: transparent;
}

.button-product-delete:hover {
  color: var(--color-error);
  background-color: transparent;
}

.button-product-save:hover {
  color: var(--color-success);
  background-color: transparent;
}

.button-product-cancel:hover {
  color: var(--color-primary-dark);
  background-color: transparent;
}

.receipt-date-picker {
  padding: 13px;
  width: 100%;
  border: 1px solid var(--color-border);
  border-radius: 4px;
  box-sizing: border-box;
}
