.views-modal-content {
  position: fixed;
  top: 0;
  left: 0;
  background-color: var(--color-background);
  box-shadow: 10px 4px 18px rgba(0, 0, 0, 0.2);
  padding: 17px;
  border-radius: 10px;
  min-width: 60%;
  width: 65%;
  max-width: 80%;
  max-height: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  animation: slideInLeft 0.5s ease forwards;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.views-modal-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 7px;
}

.views-modal-header h2 {
  margin: 0;
  color: var(--color-primary-dark);
}

.views-modal-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--color-secondary-dark);
  background-color: var(--color-secondary-light);
  padding: 5px;
  margin-bottom: 7px;
  border: none;
  border-radius: 9px;
}

.views-modal-info p {
  display: flex;
  gap: 10px;
  margin: 0;
  color: var(--color-primary-dark);
}

.views-modal-close {
  padding: 10px;
  background-color: var(--color-primary-dark);
  color: var(--color-text-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.views-modal-close:hover {
  background-color: var(--color-secondary-dark);
  color: var(--color-primary-light);
}

.views-mini-table-container {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  max-height: 75vh;
  margin: 0px;
}

.views-mini-table {
  width: 100%;
  border-collapse: collapse;
  color: var(--color-text-dark);
}

.views-mini-table td {
  border: 1px solid transparent;
  padding: 10px;
  text-align: left;
}

.views-mini-table th {
  position: sticky;
  top: 0;
  background-color: var(--color-primary-light);
  text-align: left;
  border: 1px solid transparent;
  font-weight: 600;
  vertical-align: middle;
  padding: 10px;
  color: var(--color-text-dark);
}

.views-mini-table th:first-child,
.views-mini-table td:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.views-mini-table th:last-child,
.views-mini-table td:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.views-mini-table tr:nth-child(even) {
  background-color: var(--color-secondary-light);
}

.views-mini-table tr:nth-child(odd) {
  background-color: var(--color-background);
}

.views-mini-table tr:hover {
  color: var(--color-highlight-dark);
  background-color: var(--color-input-bg);
  border: 1px solid var(--color-highlight-dark);
}

.views-mini-table-container::-webkit-scrollbar {
  width: 15px;
}

.views-mini-table-container::-webkit-scrollbar-track {
  background: var(--color-primary-light);
  border-radius: 10px;
}

.views-mini-table-container::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary-dark);
  border-radius: 10px;
  border: 3px solid var(--color-primary-light);
}

.views-mini-table-container::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-primary-dark);
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100%);
    opacity: 1;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideOutLeft {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(-100%);
    opacity: 1;
  }
}
