.statistics-container {
  padding: 10px;
  background-color: var(--color-secondary-light);
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 10px;
  height: 100vh;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.statistics-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 7px;
}

.statistics-main {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  height: 96vh;
  overflow: auto;
  gap: 8px;
}

.statistics-container h3 {
  margin: 0px;
  color: var(--color-primary-dark);
}

.statistics-container p {
  margin-bottom: 0px;
  color: var(--color-secondary-dark);
}
.controls-container {
  display: flex;
  flex-direction: column;
  gap: 0px;
}
.filter-container {
  display: flex;
  gap: 5px;
  margin: 0px;
  font-size: 12px;
}

.filter-container select,
.filter-container .react-select__control {
  width: 100%;
  max-width: 300px;
  margin: 0px;
}

.chart-container {
  display: flex;
  flex-direction: column;
  gap: 7px;
}

.chart {
  flex: 1;
}

.completion-percentage {
  margin: 0px;
  text-align: center;
  color: var(--color-secondary-dark);
}

.product-filters {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 20px;
}

.product-container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  min-width: 40%;
  border: 1px solid var(--color-border);
  border-radius: 9px;
  max-height: 90vh;
}

.product-filters label {
  cursor: pointer;
}

.product-filters input {
  margin-right: 5px;
}

.toggle-products-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  width: 100%;
  background-color: var(--color-primary-light);
  color: var(--color-primary-dark);
  border: none;
  border-radius: 7px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.toggle-products-button:hover {
  background-color: var(--color-hover-row);
}

.product-list {
  width: 100%;
  margin-top: 5px;
  padding: 0 10px;
  overflow-y: auto;
  transition: max-height 0.5s ease-out;
  max-height: calc(90vh - 250px);
}

.product-list.show {
  max-height: calc(90vh - 250px);
}
.product-list::-webkit-scrollbar {
  width: 10px;
  border-radius: 10px;
}

.product-list::-webkit-scrollbar-track {
  background: var(--color-primary-light);
  border-radius: 10px;
}

.product-list::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary-dark);
  border-radius: 6px;
}

.product-list::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-primary-dark);
}

.product-list ul {
  list-style-type: none;
  padding: 0;
}

.product-list li {
  background-color: var(--color-secondary-light);
  padding: 7px 10px;
  border: 1px solid var(--color-border);
  margin-bottom: 5px;
  border-radius: 5px;
}

.product-list li a {
  color: var(--color-highlight-dark);
  font-weight: 600;
  text-decoration: none;
}

.product-list li a:hover {
  text-decoration: none;
  color: var(--color-highlight-light);
}

.statistics-close-button {
  display: block;
  padding: 10px;
  background-color: var(--color-primary-dark);
  color: var(--color-background);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.statistics-close-button:hover {
  background-color: var(--color-secondary-dark);
}
