/* UserDashboard.css */
.menu-container {
  position: relative;
}
h2 {
  margin-top: 0;
}
.menu-button {
  padding: 7px 10px;
  background-color: transparent;
  color: var(--color-secondary-dark);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: large;
  transition: background-color 0.3s ease;
}

.menu-button:hover {
  background-color: var(--color-primary-light);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: large;
  color: var(--color-secondary-dark);
  transition: 0.3s ease;
}

.menu-button .icons {
  color: var(--color-secondary-light);
  transition: 0.3s ease;
}
.menu-button:hover .icons {
  color: var(--color-secondary-dark);
  transition: 0.3s ease;
}

.menu-options {
  position: absolute;
  top: 132%;
  left: 0%;
  background-color: var(--color-background);
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  list-style: none;
  padding: 0;
  margin: 0;
  transition: background-color 0.3s ease;
  z-index: 9999;
}

.menu-option {
  display: flex;
  gap: 20px;
  padding: 10px 20px;
  border-radius: 8px;
  color: var(--color-secondary-dark);
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  animation: fadeIn 0.3s ease-in-out;
}

.menu-option:hover {
  background-color: var(--color-text-light);
  border-radius: 8px;
  transition: 0.3s ease;
}
.menu-option:hover .icons {
  color: var(--color-text-dark);
  transition: 0.3s ease;
}

.icons {
  color: #aaa;
  transition: 0.3s ease;
  width: calc((100vw - 480px) / (1280 - 480) * (16 - 11) + 11px);
}
.headers-block-info {
  display: flex;
  margin-right: auto;
}

.block-info {
  margin: 0;
  font-size: smaller;
  padding: 10px;
  background-color: rgba(255, 166, 0, 0.1);
  color: var(--color-secondary-dark);
  border-radius: 10px;
}
.block-info .icons {
  color: orange;
}

.block-info p {
  margin: 0;
  color: orange;
}
.header-users button {
  align-items: center;
  display: flex;
  margin: 0;
  padding: 5px 7px;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}
.header-users .icons-button {
  align-items: center;
  display: flex;
  width: calc((100vw - 480px) / (1280 - 480) * (15 - 11) + 11px);
}
.header-users button p {
  margin: 0;
  vertical-align: middle;
  margin-left: 5px;
}
.user-dashboard-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  background-color: var(--color-primary-light);
}

.header-users {
  background-color: var(--color-primary-dark);
  color: var(--color-text-light);
  padding: 3px 7px;
  gap: 7px;
  text-align: center;
  display: flex;
  align-items: center;
}

.sign-out-button,
.view-products-button {
  padding: 10px;
  background-color: var(--color-primary-light);
  color: var(--color-text-dark);
  border: 1px solid var(--color-secondary-dark);
  border-radius: 8px;
  font-weight: normal;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.show-regulations-button {
  padding: 10px;
  background-color: rgba(255, 166, 0, 0.3);
  color: orange;
  border: none;
  border-radius: 8px;
  font-weight: normal;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.create-request-button {
  padding: 10px;
  background-color: var(--color-highlight-dark);
  color: var(--color-text-light);
  border: none;
  border-radius: 8px;
  font-weight: normal;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.sign-out-button:hover,
.view-products-button:hover {
  background-color: var(--color-secondary-light);
  color: var(--color-primary-dark);
}
.show-regulations-button .icons {
  color: orange;
}
.show-regulations-button:hover .icons {
  color: orange;
}
.create-request-button:hover {
  background-color: var(--color-highlight-light);
  color: var(--color-text-light);
}

.custom-toast-container {
  min-width: 350px;
}

.custom-toast {
  background-color: var(--color-primary-light);
  color: var(--color-text-light);
  border-radius: 10px;
  padding: 16px;
  box-shadow: 0 0 17px rgba(0, 0, 0, 0.9);
}

.custom-toast-body {
  font-size: calc((100vw - 480px) / (1280 - 480) * (16 - 14) + 14px);
  display: flex;
  align-items: center;
}

.custom-toast .message-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.custom-toast .icons {
  margin-right: 8px;
  color: var(--color-success); /*   */
}

.custom-toast .message-info .icons {
  color: var(--color-success);
}

.custom-toast .Toastify__close-button {
  color: var(--color-secondary-dark);
}
