.admin-panel {
  background-color: var(--color-secondary-light);
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 20px;
  padding: 17px;
  border-radius: 9px;
  height: 85vh;
  overflow: hidden;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
}

.admin-panel h2 {
  margin: 0;
  text-align: center;
  color: var(--color-secondary-dark);
}

.toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.btn-delete-selected {
  display: flex;
  gap: 5px;
  align-items: center;
  background-color: var(--color-primary-light);
  color: var(--color-text-dark);
  padding: 10px 7px;
  border: none;
  border-radius: 7px;
  cursor: pointer;
}

.btn-delete-selected:hover .icons {
  color: var(--color-error);
}

.btn-close {
  background-color: var(--color-secondary-dark);
  color: var(--color-text-light);
  padding: 10px;
  border: none;
  border-radius: 9px;
  cursor: pointer;
}

.btn-close:hover {
  background-color: var(--color-primary-dark);
}

.edit-wrapper {
  display: flex;
  justify-content: flex-end;
  gap: 5px;
}

.edit-wrapper .btn {
  background-color: transparent;
  padding: 2px 7px;
  border: none;
  border-radius: 7px;
  cursor: pointer;
}

.edit-wrapper .btn .icons {
  color: var(--color-secondary-dark);
}

.btn:hover {
  background-color: var(--color-background);
  color: var(--color-highlight-dark);
  border-radius: 7px;
}

.btn:hover .icons {
  color: var(--color-highlight-dark);
}

.edit-input {
  width: 100%;
  padding: 12px 7px;
  background-color: var(--color-background);
  border: 1px solid var(--color-highlight-light);
  border-radius: 7px;
}
.admin-table-container {
  height: 62vh;
  overflow: auto;
}

.admin-table-container::-webkit-scrollbar {
  width: 13px;
}

.admin-table-container::-webkit-scrollbar-track {
  background: var(--color-primary-light);
  border-radius: 10px;
}

.admin-table-container::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary-dark);
  border-radius: 10px;
  border: 3px solid var(--color-primary-light);
}

.admin-table-container::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-primary-dark);
}

.admin-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 7px;
}

.admin-table th,
.admin-table td {
  border: 1px solid var(--color-primary-light);
  padding: 2px;
  text-align: left;
}

.admin-table th {
  background-color: var(--color-primary-light);
  text-align: center;
  font-weight: bold;
  vertical-align: middle;
}

.admin-table th:hover {
  color: inherit; /*      */
  background-color: var(--color-primary-light); /*     */
}

.admin-table tr:nth-child(even) {
  background-color: var(--color-secondary-light);
}

.admin-table tr:hover {
  background-color: var(--color-input-bg);
  border: 1px solid var(--color-highlight-dark);
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
}

.pagination button,
.pagination input,
.pagination select {
  margin: 0 5px;
  padding: 10px;
  border: 1px solid var(--color-border);
  border-radius: 5px;
  font-size: 14px;
}

.pagination button:disabled {
  background: var(--color-secondary-light);
  cursor: not-allowed;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: var(--color-background);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.modal-content h3 {
  margin-bottom: 10px;
}

.modal-content button {
  background-color: var(--color-secondary-dark);
  color: var(--color-text-light);
  margin: 0 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.modal-content button:hover {
  background-color: var(--color-primary-dark);
  color: var(--color-text-light);
}
.search-input {
  background-color: var(--color-primary-light);
  min-width: 40%;
  padding: 9px;
  margin-left: 10px;
  border: 1px solid var(--color-primary-light);
  border-radius: 8px;
}
