/* Regulations.css */
.fade-in {
  animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.regulations-content {
  position: absolute;
  padding: 20px;
  top: 5%;
  left: 15%;
  z-index: 8000;
  margin-top: 20px;
  width: 70%;
  height: 90vh;
  border: 1px solid var(--color-border);
  border-radius: 20px;
  background-color: var(--color-secondary-light);
  box-shadow: -5px 4px 28px rgba(0, 0, 0, 0.1);
  color: var(--color-text-dark);
  overflow-y: scroll;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.regulations-content::-webkit-scrollbar {
  width: 12px;
}

.regulations-content::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.regulations-content::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.regulations-content::-webkit-scrollbar-thumb:hover {
  background: var(--color-secondary-dark);
}

.regulations-section {
  margin-bottom: 20px;
  border-bottom: 1px solid var(--color-border);
  padding-bottom: 10px;
  text-align: start;
}

.regulations-section h5 {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.regulations-section-content {
  margin-top: 10px;
  padding: 20px;
  background-color: var(--color-background);
  border-radius: 20px;
}

.regulations-section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  font-size: 16px;
  color: var(--color-highlight-dark);
}

.regulations-section p {
  line-height: 1.6;
  margin-bottom: 10px;
}

.regulations-section ul {
  list-style: disc;
  margin-left: 20px;
}

.regulations-section li {
  margin-bottom: 5px;
}

.regulations-section strong {
  font-weight: bold;
}

.regulations-section em {
  font-style: italic;
}
