.request-table-container,
.products-table-container {
  width: 100%;
  height: 98vh;
  padding: 10px;
  background-color: var(--color-background);
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}

.request-table-container::-webkit-scrollbar,
.products-table-container::-webkit-scrollbar {
  width: 10px;
}

.request-table-container::-webkit-scrollbar-track,
.products-table-container::-webkit-scrollbar-track {
  background: var(--color-primary-light);
  border-radius: 6px;
}

.request-table-container::-webkit-scrollbar-thumb,
.products-table-container::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary-dark);
  border-radius: 6px;
}

.request-table-container::-webkit-scrollbar-thumb:hover,
.products-table-container::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-primary-dark);
}

.table-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0 7px 0;
}

.request-table,
.products-table {
  width: 100%;
  border-collapse: collapse;
  color: var(--color-text-dark);
}

.request-table td,
.products-table td {
  border: 1px solid transparent;
  padding: 7px;
  text-align: left;
}

.request-table th,
.products-table th {
  background-color: var(--color-primary-light);
  text-align: left;
  border: 1px solid transparent;
  font-weight: 600;
  border-bottom: none;
  vertical-align: bottom;
  padding: 9px;
  color: var(--color-text-dark);
  position: sticky;
  top: 0;
  z-index: 1;
}
.header-columns {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.request-table th:first-child,
.request-table td:first-child,
.products-table th:first-child,
.products-table td:first-child {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.request-table th:last-child,
.request-table td:last-child,
.products-table th:last-child,
.products-table td:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.request-table tr:nth-child(even),
.products-table tr:nth-child(even) {
  background-color: var(--color-secondary-light);
}

.request-table tr:nth-child(odd),
.products-table tr:nth-child(odd) {
  background-color: var(--color-background);
}

.request-table tr:hover,
.products-table tr:hover {
  color: var(--color-highlight-dark);
  background-color: var(--color-input-bg);
  border: 1px solid var(--color-highlight-dark);
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-text-dark);
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  cursor: pointer;
  background-color: var(--color-background);
  border: 1px solid var(--color-border);
  border-radius: 6px;
  color: var(--color-text-dark);
  transition: background-color 0.3s ease;
}

.pagination button:hover {
  background-color: var(--color-text-light);
  border: 1px solid transparent;
}

.pagination button:disabled {
  background-color: #eee;
  cursor: not-allowed;
}

.pagination input {
  margin-left: 5px;
  width: 40px;
  text-align: center;
  background-color: var(--color-background);
  border: 1px solid var(--color-border);
  border-radius: 6px;
  color: var(--color-text-dark);
  padding: 8px;
}

.pagination select {
  margin-left: 10px;
  background-color: var(--color-background);
  border: 1px solid var(--color-border);
  border-radius: 6px;
  color: var(--color-text-dark);
  padding: 8px;
}

.multi-select-filter {
  padding: 0px;
  margin: 0px;
  border: none;
}

.action-controls button {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 8px;
  background-color: transparent;
  border: 1px solid var(--color-border);
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.action-controls button:hover,
.pagination button:hover {
  background-color: var(--color-primary-light);
  border: 1px solid transparent;
}
.action-controls button:hover .action-icon,
.pagination button:hover .action-icon {
  color: var(--color-highlight-light);
}
.container-requests-search {
  position: relative;
  flex-basis: 30%;
}

.input-requests-search {
  padding: 8px 8px 8px 35px;
  background-color: var(--color-primary-light);
  border: 1px solid var(--color-border);
  border-radius: 6px;
  width: calc(100% - 5px);
  font-size: calc((100vw - 480px) / (1280 - 480) * (13 - 11) + 11px);
}
.filter-input {
  width: 100%;
  border-radius: 4px;
  border: 1px solid var(--color-border);
  padding: 10px 7px;
}
.input-requests-search:focus {
  outline: none;
  background-color: var(--color-background);
  border: 1px solid var(--color-highlight-dark);
  transition: background-color 0.3s ease;
}

.action-controls {
  display: flex;
  align-items: center;
  gap: 8px;
}

.action-icons {
  display: flex;
  gap: 8px;
}

.action-icon {
  cursor: pointer;
  color: var(--color-secondary-dark);
  transition: color 0.3s ease;
  width: calc((100vw - 480px) / (1280 - 480) * (16 - 12) + 12px);
  height: auto;
}
.action-icon:hover {
  color: var(--color-highlight-light);
}
.lock {
  color: var(--color-error);
}

.open {
  color: var(--color-border);
}

.icon-search {
  position: absolute;
  top: 30%;
  left: 10px;
  color: var(--color-secondary-dark);
}
.sort-icons {
  transform: rotate(140deg);
}
.icon-wrapper {
  position: relative;
}

.icon-tooltip {
  visibility: hidden;
  background-color: var(--color-tooltip-bg);
  color: var(--color-tooltip-text);
  text-align: center;
  border-radius: 6px;
  padding: 7px;
  min-width: 100px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 0%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.icon-wrapper:hover .icon-tooltip {
  visibility: visible;
  opacity: 1;
}

.file-icon-wrapper {
  position: relative;
  display: inline-block;
  cursor: pointer;
  color: var(--color-highlight-light);
}

.file-download-tooltip {
  visibility: hidden;
  background-color: var(--color-tooltip-bg);
  color: var(--color-tooltip-text);
  text-align: center;
  border-radius: 6px;
  padding: 7px;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.file-icon-wrapper:hover .file-download-tooltip {
  visibility: visible;
  opacity: 1;
}
.link-request {
  text-decoration: none;
  font-weight: 600;
  color: var(--color-highlight-dark);
}

/* Responsive styles */
@media (max-width: 768px) {
  .request-table-container,
  .products-table-container {
    width: 100%;
    padding: 10px;
    overflow-x: scroll;
  }

  .request-table th,
  .request-table td,
  .products-table th,
  .products-table td {
    padding: 8px;
  }

  .pagination input,
  .pagination select {
    margin: 5px 0;
    border: 1px solid var(--color-border);
  }

  .request-table-container::-webkit-scrollbar,
  .products-table-container::-webkit-scrollbar {
    width: 10px;
  }

  .request-table-container::-webkit-scrollbar-track,
  .products-table-container::-webkit-scrollbar-track {
    background: var(--color-secondary-light);
    border-radius: 6px;
  }

  .request-table-container::-webkit-scrollbar-thumb,
  .products-table-container::-webkit-scrollbar-thumb {
    background-color: var(--color-border);
    border-radius: 6px;
  }

  .request-table-container::-webkit-scrollbar-thumb:hover,
  .products-table-container::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-primary-dark);
  }
}
